const createTrayCollideFoodSystem = () => {
  return {
    update(delta, worldState, { batch, camera, width, height }) {
      const {
        recipes,
        trayState,
        foods,
        collection,
        stateTime,
        life,
        whiteSmokeList,
      } = worldState;

      for (let i = 0; i < foods.length; i++) {
        if (
          foods[i].x > trayState.x - trayState.width / 2 &&
          foods[i].x < trayState.x + trayState.width / 2 &&
          foods[i].y >
            // trayState.y - trayState.height - trayState.offsetCheckY &&
            trayState.y - trayState.height &&
          foods[i].y < trayState.y + trayState.height &&
          stateTime > foods[i].upTime
        ) {
          for (let j = 0; j < recipes.length; j++) {
            if (foods[i].type === recipes[j].type && !worldState.correctCatch) {
              worldState.correctCatch = true;
              if (recipes[j].counter === 0) {
                // only push that kind of food one time to collection
                // trayState.offsetCheckY += trayState.height * 0.7; // update offsetY
                collection.push({
                  type: foods[i].type,
                  stateTimeSmoke: 0,
                  stateTimeFood: 0,
                  startSmoke: true,
                });

                whiteSmokeList.push({
                  inPot: true,

                  stateTime: 0,
                  // width: trayState.width,
                  // height: trayState.height,
                });
              } else {
                whiteSmokeList.push({
                  x: foods[i].x,
                  y: foods[i].y,
                  stateTime: 0,
                  scale: foods[i].oriScale,
                });
              }

              recipes[j].counter = Math.min(
                recipes[j].amount,
                (recipes[j].counter += 1)
              );

              if (recipes[j].counter === recipes[j].amount) {
                recipes[j].finished = true;
              }
              //   console.log("hit");
              foods.splice(i, 1);
              break; // if there is any one match => break the loop
            } else if (foods[i].type !== recipes[j].type) {
              //   console.log("miss");
            }

            //wrong catch
            if (j === recipes.length - 1) {
              worldState.comboCounter = 0;
              worldState.comboCheckIndex.length = 0;
              life[worldState.lifeIndex].red = false;
              worldState.lifeIndex = Math.max(0, (worldState.lifeIndex -= 1));
              worldState.stunScreen.check = true;
              foods.splice(i, 1);
            }
          }
        }
      }
    },
  };
};

export default createTrayCollideFoodSystem;
