const createFoodRenderSystem = () => {
  let FOOD_WIDTH, FOOD_HEIGHT;

  return {
    init({ foodTex }, { width, height }) {
      let tmpFood;
      for (let tex of foodTex) {
        if (tex.index === 0) {
          tmpFood = tex.image;
          break;
        }
      }
      FOOD_WIDTH = width / 6;
      FOOD_HEIGHT = (FOOD_WIDTH * tmpFood.height) / tmpFood.width;
    },

    update(delta, { foods, foodTex }, { width, height, batch, camera }) {
      batch.setProjection(camera.combined);
      batch.begin();
      for (let i = foods.length - 1; i >= 0; i--) {
        let image;
        for (let j = 0; j < foodTex.length; j++) {
          if (foodTex[j].index === foods[i].type) {
            image = foodTex[j].image;
          }
        }

        foods[i].oriOpacity = Math.min(
          (foods[i].oriOpacity += delta * foods[i].opacitySpeed),
          1
        );
        foods[i].oriScale = Math.min(
          (foods[i].oriScale += delta * foods[i].scaleSpeed),
          1
        );
        batch.setColor(1, 1, 1, foods[i].oriOpacity);
        image.draw(
          batch,
          foods[i].x - FOOD_WIDTH / 2,
          foods[i].y,
          FOOD_WIDTH,
          FOOD_HEIGHT,
          FOOD_WIDTH / 2,
          FOOD_HEIGHT / 2,
          0,
          foods[i].oriScale,
          foods[i].oriScale
        );
      }
      batch.end();
    },
  };
};

export default createFoodRenderSystem;
