const createFoodPositionSystem = () => {
  return {
    init(worldState, extra) {},

    update(delta, worldState, { width, height }) {
      const { foods, redSmokeList, recipes } = worldState;

      for (let i = 0; i < foods.length; i++) {
        if (foods[i].upSpeedY > 0) {
          foods[i].upSpeedY =
            foods[i].oriSpeedY + foods[i].upAccelerationY * foods[i].stateTime;
          foods[i].upSpeedX =
            foods[i].oriSpeedX + foods[i].upAccelerationX * foods[i].stateTime;

          foods[i].stateTime = Math.min(
            (foods[i].stateTime += delta),
            foods[i].upRealTime
          );

          foods[i].y -= foods[i].upSpeedY * delta;
          foods[i].x += foods[i].upSpeedX * delta;
        } else {
          foods[i].y += foods[i].downSpeedY * delta;
          foods[i].downSpeedY += foods[i].downAcceleration;
        }

        if (foods[i].y > (height * 4.1) / 5) {
          for (let j = 0; j < recipes.length; j++) {
            if (foods[i].type === recipes[j].type) {
              worldState.comboCounter = 0;
              worldState.comboCheckIndex.length = 0;
            }
          }
          redSmokeList.push({
            x: foods[i].x,
            y: foods[i].y,
            stateTime: 0,
            scale: foods[i].oriScale,
          });
          foods.splice(i, 1);
        }
      }
    },
  };
};

export default createFoodPositionSystem;
