import { Vector2 } from "gdxjs";

const createTrayPositionSystem = () => {
  let tmp = new Vector2();

  return {
    update(delta, { trayState }, { batch, width, height, camera }) {
      tmp
        .setVector(trayState.target)
        .subVector(trayState.position)
        .nor()
        .scale(trayState.speed * delta);
      if (trayState.target.distanceSqr(trayState.position) <= tmp.len2()) {
        trayState.position.setVector(trayState.target);
      } else {
        trayState.position.addVector(tmp);
      }

      trayState.x = trayState.position.x;
      if (trayState.x <= trayState.width / 2) {
        trayState.x = trayState.width / 2;
      }
      if (trayState.x >= width - trayState.width / 2) {
        trayState.x = width - trayState.width / 2;
      }
    },
  };
};

export default createTrayPositionSystem;
