import React from "react";
import ReactDOM from "react-dom";
import RegisterModal from "../RegisterModal";

import { createViewportAwareInputHandler, fillRect, Vector2 } from "gdxjs";
import { checkPointInsideRect } from "./utils/coordUtils";
import axios from "../../utils/axios";
import analytics from "../../utils/analytics";
import createInstructionScreen from "./InstructionScreen";

const createStartScreenSystem = (Game) => {
  const goToInstructionScreen = async () => {
    Game.setCurrentScreen(await createInstructionScreen(Game));
  };

  const { canvas, viewport, width, height, camera, batch, assets } = Game;

  const inputHandler = createViewportAwareInputHandler(canvas, viewport);

  const atlas = assets.startScreen;
  const bg = assets.backGround2;

  const banner = atlas.findRegion("1nam-an-lau-mien-phi", -1);
  const text = atlas.findRegion("content", -1);
  const logo = atlas.findRegion("logo", -1);
  const button = atlas.findRegion("button-play", -1);
  const TEXT_WIDTH = width * 0.9;
  const TEXT_HEIGHT = (TEXT_WIDTH * text.height) / text.width;

  const LOGO_WIDTH = (width * 2) / 3;
  const LOGO_HEIGHT = (LOGO_WIDTH * logo.height) / logo.width;

  const BANNER_WIDTH = width * 0.96;
  const BANNER_HEIGHT = (BANNER_WIDTH * banner.height) / banner.width;

  const BUTTON_WIDTH = width / 3.5;
  const BUTTON_HEIGHT = (BUTTON_WIDTH * button.height) / button.width;

  const buttonInfos = [
    width / 2 - BUTTON_WIDTH / 2,
    (height * 1.6) / 3,
    BUTTON_WIDTH,
    BUTTON_HEIGHT,
  ];

  const coord = new Vector2();
  let point;
  inputHandler.addEventListener("touchStart", (x, y) => {
    coord.setVector(inputHandler.getTouchedWorldCoord());
    point = { x: coord.x, y: coord.y };
    if (checkPointInsideRect(point, buttonInfos)) {
      analytics.track("startGame");
      const token = localStorage.getItem("@Token");
      if (!token) {
        analytics.track("newUser");
        const wrapper = document.createElement("div");
        document.body.append(wrapper);
        ReactDOM.render(
          <RegisterModal
            onSubmit={async (formBody) => {
              try {
                const res = await axios.post("/register", formBody);
                const token = res.data;
                localStorage.setItem("@Token", token);
                document.body.removeChild(wrapper);
                analytics.identify(token);
                goToInstructionScreen();
              } catch (e) {}
            }}
          />,
          wrapper
        );
      } else {
        analytics.identify(token);
        goToInstructionScreen();
      }
    }
  });

  const update = (delta) => {
    batch.setProjection(camera.combined);
    batch.begin();
    batch.setColor(1, 1, 1, 1);
    fillRect(batch, bg, 0, 0, width, height);
    logo.draw(
      batch,
      width / 2 - LOGO_WIDTH / 2,
      height / 30,
      LOGO_WIDTH,
      LOGO_HEIGHT
    );
    text.draw(
      batch,
      width / 2 - TEXT_WIDTH / 2,
      (height * 0.95) / 3,
      TEXT_WIDTH,
      TEXT_HEIGHT
    );
    banner.draw(
      batch,
      width / 2 - BANNER_WIDTH / 2,
      (height * 1.9) / 3,
      BANNER_WIDTH,
      BANNER_HEIGHT
    );
    button.draw(
      batch,
      buttonInfos[0],
      buttonInfos[1],
      buttonInfos[2],
      buttonInfos[3]
    );
    batch.end();
  };

  return {
    update,
    destroy() {
      inputHandler.cleanup();
    },
  };
};

export default createStartScreenSystem;
