import { fillRect } from "gdxjs";

const createBackGroundRenderSystem = () => {
  let bg, bar;
  let BAR_WIDTH, BAR_HEIGHT;
  return {
    init(worldState, { width, assets }) {
      let atlas = assets.newAssets;

      bg = assets.backGround;
      bar = atlas.findRegion("menu-bar", -1);
      BAR_WIDTH = width;
      BAR_HEIGHT = (BAR_WIDTH * bar.height) / bar.width;
    },
    update(delta, worldState, { batch, width, height, camera }) {
      batch.setProjection(camera.combined);
      batch.begin();
      batch.setColor(1, 1, 1, 1);

      fillRect(batch, bg, 0, 0, width, height);
      bar.draw(batch, 0, 0, BAR_WIDTH, BAR_HEIGHT);

      batch.end();
    },
  };
};

export default createBackGroundRenderSystem;
