const createCollectionRenderSystem = () => {
  let COLLECTION_WIDTH, COLLECTION_HEIGHT;
  // let SMOKE_WIDTH, SMOKE_HEIGHT;
  let foodInPotTex;
  let foodPosition;
  return {
    init({ trayState }, { width, assets }) {
      COLLECTION_WIDTH = width / 7;
      COLLECTION_HEIGHT = (COLLECTION_WIDTH * 255) / 400;

      // SMOKE_WIDTH = COLLECTION_WIDTH * 4;
      // SMOKE_HEIGHT = COLLECTION_HEIGHT * 4;
    },
    update(
      delta,
      { trayState, foodAnimation, smokeAnimation, collection, foodInPot },
      { batch, width, height, camera }
    ) {
      foodPosition = [
        {
          x: trayState.x - COLLECTION_WIDTH / 2,
          y: trayState.y - trayState.height / 1.4,
        },
        {
          x: trayState.x - trayState.width / 2.2,
          y: trayState.y - trayState.height / 1.4,
        },
        {
          x: trayState.x + trayState.width / 2.4 - COLLECTION_WIDTH,
          y: trayState.y - trayState.height / 1.4,
        },
        {
          x: trayState.x - COLLECTION_WIDTH / 2,
          y: trayState.y - trayState.height * 1.2,
        },
      ];
      batch.setProjection(camera.combined);
      batch.begin();
      batch.setColor(1, 1, 1, 1);
      for (let i = collection.length - 1; i >= 0; i--) {
        for (let j = 0; j < foodInPot.length; j++) {
          if (collection[i].type === foodInPot[j].index) {
            foodInPotTex = foodInPot[j];
          }
        }

        foodInPotTex.image.draw(
          batch,
          foodPosition[i].x,
          foodPosition[i].y,
          COLLECTION_WIDTH,
          COLLECTION_HEIGHT
        );
      }

      batch.end();
    },
  };
};

export default createCollectionRenderSystem;
