import { AlignMode, TextType } from "../utils/loadFontFile";

const createRecipeRenderSystem = () => {
  let FOOD_WIDTH, FOOD_HEIGHT;
  let recipePositionInfos = [];
  let TEXT_AMOUNT1, TEXT_AMOUNT2, TEXT_AMOUNT3, TEXT_AMOUNT4;
  let TEXT_COUNTER, TEXT_LIST;
  let numberFont;
  return {
    init({ foodTex }, { width, height, font }) {
      let tmpFood;
      for (let tex of foodTex) {
        if (tex.index === 0) {
          tmpFood = tex.image;
          break;
        }
      }
      FOOD_WIDTH = width / 7;
      FOOD_HEIGHT = (FOOD_WIDTH * tmpFood.height) / tmpFood.width;

      const RECIPE_MARGIN = width / 75;
      for (let i = 0; i < 4; i++) {
        recipePositionInfos.push({
          x: width / 3.06 + i * (FOOD_WIDTH + RECIPE_MARGIN),
          y: height * 0.028,
        });
      }

      numberFont = font.numberFont;
      TEXT_COUNTER = numberFont.setNewText(width);
      TEXT_COUNTER.setAlignMode(AlignMode.right);
      TEXT_COUNTER.setType(TextType.dynamic);
    },

    update(
      delta,
      {
        recipes,
        foodTex,
        whiteSmokeList,
        smokeAnimation,
        stateTime,
        genderNewRecipeAmount,
      },
      { batch, camera, width, height, font }
    ) {
      if (genderNewRecipeAmount) {
        genderNewRecipeAmount = false;
        TEXT_AMOUNT1 = numberFont.setNewText(width);
        TEXT_AMOUNT1.setAlignMode(AlignMode.left);
        TEXT_AMOUNT2 = numberFont.setNewText(width);
        TEXT_AMOUNT2.setAlignMode(AlignMode.left);
        TEXT_AMOUNT3 = numberFont.setNewText(width);
        TEXT_AMOUNT3.setAlignMode(AlignMode.left);
        TEXT_AMOUNT4 = numberFont.setNewText(width);
        TEXT_AMOUNT4.setAlignMode(AlignMode.left);
        TEXT_LIST = [TEXT_AMOUNT1, TEXT_AMOUNT2, TEXT_AMOUNT3, TEXT_AMOUNT4];
      }

      batch.setProjection(camera.combined);
      batch.begin();
      for (let i = 0; i < recipes.length; i++) {
        let image;
        for (let j = 0; j < foodTex.length; j++) {
          if (foodTex[j].index === recipes[i].type) {
            image = foodTex[j].image;
          }
        }
        batch.setColor(1, 1, 1, 1);
        image.draw(
          batch,
          recipePositionInfos[i].x,
          recipePositionInfos[i].y,
          FOOD_WIDTH,
          FOOD_HEIGHT
        );
      }
      for (let i = 0; i < 4; i++) {
        batch.setColor(0, 0, 0, 1);
        TEXT_LIST[i].draw(
          batch,
          `/ ${recipes[i].amount}`,
          recipePositionInfos[i].x * 0.97 + FOOD_WIDTH / 2 + width * 0.005,
          height * 0.06,
          width / 20
        );
        TEXT_COUNTER.draw(
          batch,
          `${recipes[i].counter}`,
          recipePositionInfos[i].x * 0.98 +
            FOOD_WIDTH / 2 +
            width * 0.0003 -
            width * 0.01,
          height * 0.06,
          width / 20
        );
        batch.setColor(1, 1, 1, 1);
      }
      batch.end();
    },
  };
};

export default createRecipeRenderSystem;
