const pushToComboList = (
  comboList,
  texture,
  indexCombo,
  comboSize,
  width,
  height
) => {
  comboList.push({
    tex: texture,
    x: Math.random() > 0.5 ? (width * 1.5) / 4 : (width * 2.5) / 4,
    y:
      Math.random() * ((height * 2) / 4 - (height * 2.5) / 4) +
      (height * 2.5) / 4,
    width: comboSize[indexCombo].width,
    height: comboSize[indexCombo].height,
    infos: { stateTime: 0, scale: 0, opacity: 0 },
  });
};

const createBannerComboRenderSystem = () => {
  let comboList = [];
  let combo1, combo2, combo3;
  let comboSize = [];

  return {
    init(worldState, { assets, width, height }) {
      const atlas = assets.newAssets;

      combo1 = atlas.findRegion("combo1", -1);
      combo2 = atlas.findRegion("combo2", -1);
      combo3 = atlas.findRegion("combo3", -1);

      const C1_WIDTH = width / 3;
      const C1_HEIGHT = (C1_WIDTH * combo1.height) / combo1.width;

      const C2_WIDTH = width / 2.5;
      const C2_HEIGHT = (C2_WIDTH * combo2.height) / combo2.width;

      const C3_WIDTH = width / 2;
      const C3_HEIGHT = (C3_WIDTH * combo3.height) / combo3.width;
      comboSize = [
        { width: C1_WIDTH, height: C1_HEIGHT },
        { width: C2_WIDTH, height: C2_HEIGHT },
        { width: C3_WIDTH, height: C3_HEIGHT },
      ];
    },
    update(delta, worldState, { batch, camera, width, height }) {
      const { comboCounter, comboState } = worldState;
      if (
        comboCounter === 10 &&
        worldState.comboCheckIndex[comboCounter] === undefined
      ) {
        worldState.comboCheckIndex[comboCounter] = true;
        pushToComboList(comboList, combo1, 0, comboSize, width, height);
      } else if (
        comboCounter === 30 &&
        worldState.comboCheckIndex[comboCounter] === undefined
      ) {
        worldState.comboCheckIndex[comboCounter] = true;
        pushToComboList(comboList, combo2, 1, comboSize, width, height);
      } else if (
        comboCounter >= 50 &&
        comboCounter % 10 === 0 &&
        worldState.comboCheckIndex[comboCounter] === undefined
      ) {
        worldState.comboCheckIndex[comboCounter] = true;
        pushToComboList(comboList, combo3, 2, comboSize, width, height);
      }

      batch.setProjection(camera.combined);
      batch.begin();
      for (let i = comboList.length - 1; i >= 0; i--) {
        batch.setColor(1, 1, 1, comboList[i].infos.opacity);
        comboList[i].tex.draw(
          batch,
          comboList[i].x - comboList[i].width / 2,
          comboList[i].y,
          comboList[i].width,
          comboList[i].height,
          comboList[i].width / 2,
          comboList[i].height / 2,
          0,
          comboList[i].infos.scale,
          comboList[i].infos.scale
        );

        if (
          comboList[i].infos.scale < 1 &&
          comboList[i].infos.stateTime === 0
        ) {
          comboList[i].infos.scale = Math.min(
            (comboList[i].infos.scale += delta * comboState.scaleSpeed),
            1
          );
          comboList[i].infos.opacity = Math.min(
            (comboList[i].infos.opacity += delta * comboState.opacitySpeed),
            1
          );
        } else {
          comboList[i].infos.stateTime += delta;
          if (comboList[i].infos.stateTime > comboState.delayTime) {
            comboList[i].infos.scale = Math.max(
              (comboList[i].infos.scale -= delta * comboState.scaleSpeed),
              0
            );
            comboList[i].infos.opacity = Math.max(
              (comboList[i].infos.opacity -= delta * comboState.opacitySpeed),
              0
            );

            if (comboList[i].infos.scale === 0) {
              comboList.splice(i, 1);
            }
          }
        }
      }
      batch.end();
    },
  };
};

export default createBannerComboRenderSystem;
