const createGameScreenSystem = (Game) => {
  const {
    width,
    height,
    canvas,
    gl,
    batch,
    camera,
    whiteTex,
    assets,
    viewport,
    font,
  } = Game;

  const inputHandler = createViewportAwareInputHandler(canvas, viewport);
  const TRAY_INITIAL_Y = (height * 3.1) / 4;

  const foodTex = getFoodTexture(assets.animAssets);
  const recipes = [];

  const shuffle = (array) => {
    array.sort(() => Math.random() - 0.5);
  };

  const genderNewRecipe = () => {
    shuffle(foodTex);
    recipes.length = 0;
    for (let i = 0; i < 4; i++) {
      recipes.push({
        type: foodTex[i].index,
        // amount: Math.floor(Math.random() * (7 - 1) + 1),
        amount: Math.floor(Math.random() * (6 - 4) + 4),
        finished: false,
        aniStateTime: 0,
        counter: 0,
      });
    }
  };

  genderNewRecipe();

  const worldState = {
    score: 0,
    comboCounter: 0,
    correctCatch: false,
    genderNewRecipeAmount: true,
    stateTime: 0,
    gameOver: false,
    emptyLife: false,
    life: [{ red: true }, { red: true }, { red: true }],
    lifeIndex: 2, // life checking index
    stunScreen: { check: false, opacity: 1 },
    trayState: {
      x: width / 2,
      y: TRAY_INITIAL_Y,
      position: new Vector2(width / 2, TRAY_INITIAL_Y),
      target: new Vector2(width / 2, TRAY_INITIAL_Y),
      moveLeft: false,
      moveRight: false,
      speed: width * 2.5,
      // offsetCheckY: 0, //
    },
    difficulty: 1, //
    collection: [], //
    collectionFinished: [],
    recipes: recipes, //
    foods: [], //
    foodTex: foodTex,
    // foodAnimation: getFoodAnimation(assets.animAssets).foodAnimation,
    smokeAnimation: getFoodAnimation(assets.animAssets).smokeAnimation,
    foodInPot: getFoodInPotTexture(assets.foodInPot),
    whiteSmokeList: [], // contain position of ws when collection have that kind of food
    redSmokeList: [], // contain position of rs whenever red smoke event triggered
    comboState: {
      delayTime: 0.5,
      scaleSpeed: (1 - 0) / 0.2,
      opacitySpeed: (1 - 0) / 0.2,
    },
    comboCheckIndex: [],
  };

  const tray = assets.foodInPot.findRegion("hot_pot", -1);
  const TRAY_WIDTH = width / 3.8;
  const TRAY_HEIGHT = (TRAY_WIDTH * tray.height) / tray.width;
  worldState.trayState.width = TRAY_WIDTH;
  worldState.trayState.height = TRAY_HEIGHT;

  const extra = {
    canvas,
    width,
    height,
    gl,
    batch,
    camera,
    whiteTex,
    inputHandler,
    assets,
    font,
  };
  const world = createWorld(worldState, extra);
  world.addSystem(createInputHandlerGameScreenSystem());
  world.addSystem(createBackGroundRenderSystem());
  world.addSystem(createTrayPositionSystem());

  world.addSystem(createFoodSpawnSystem());
  world.addSystem(createFoodPositionSystem());
  world.addSystem(createFoodRenderSystem());
  world.addSystem(createRecipeRenderSystem());
  world.addSystem(createRedSmokeRenderSystem());
  world.addSystem(createCollectionRenderSystem());
  world.addSystem(createLifeRenderSystem());
  world.addSystem(createStunScreenRenderSystem());
  world.addSystem(createTrayCollideFoodSystem());
  world.addSystem(createScoreRenderSystem());
  world.addSystem(createScoreCalcSystem());
  world.addSystem(createTrayRenderSystem());
  world.addSystem(createWhiteSmokeRenderSystem());
  world.addSystem(createBannerComboRenderSystem());

  let count = 0;
  let countRemainLife = worldState.life.length;
  let oneTimeCheck = true;

  // const restartGame = async () => {
  //   Game.setCurrentScreen(await createStartScreenSystem(Game));
  // };
  const goToLeaderBoardScreen = async () => {
    Game.setCurrentScreen(await createLeaderBoardSystem(Game));
  };

  const update = (delta) => {
    if (!worldState.gameOver) {
      // check remaining life
      countRemainLife = worldState.life.length;
      for (let l of worldState.life) {
        if (!l.red) {
          countRemainLife--;
        }
      }

      if (countRemainLife === 0 && oneTimeCheck) {
        worldState.emptyLife = true;
        oneTimeCheck = false;
        for (let i = 0; i < worldState.foods.length; i++) {
          worldState.redSmokeList.push({
            x: worldState.foods[i].x,
            y: worldState.foods[i].y,
            stateTime: 0,
            scale: worldState.foods[i].oriScale,
          });
        }
        worldState.foods.length = 0;
        worldState.gameOver = true;
      }

      // check finished current recipe
      count = 0;
      for (let rep of worldState.recipes) {
        if (rep.finished) {
          count++;
        }
      }

      if (count === worldState.recipes.length) {
        for (let i = 0; i < worldState.foods.length; i++) {
          worldState.redSmokeList.push({
            x: worldState.foods[i].x,
            y: worldState.foods[i].y,
            stateTime: 0,
            scale: worldState.foods[i].oriScale,
          });
        }

        genderNewRecipe();
        worldState.genderNewRecipeAmount = true;
        worldState.foods.length = 0;
        worldState.collection.length = 0;
        worldState.difficulty += 1;
        // worldState.trayState.offsetCheckY = 0;
      }

      worldState.stateTime += delta;
      world.update(delta);
    } else {
      // inputHandler.cleanup();
      // world.update(delta, true);
      Game.score = worldState.score;
      goToLeaderBoardScreen();
      // showLeaderBoard(worldState.score, restartGame);
    }
  };

  return {
    update,
    destroy() {
      inputHandler.cleanup();
    },
  };
};

export default createGameScreenSystem;
