const createRedSmokeRenderSystem = () => {
  let SMOKE_WIDTH, SMOKE_HEIGHT;
  return {
    init({ foodTex }, { width, height }) {
      SMOKE_WIDTH = width / 2;
      SMOKE_HEIGHT = (SMOKE_WIDTH * 3) / 5;
    },

    update(delta, worldState, { batch, camera, width, height }) {
      const { redSmokeList, smokeAnimation, emptyLife } = worldState;
      batch.setProjection(camera.combined);
      batch.begin();
      batch.setColor(1, 0, 0, 1);
      for (let i = redSmokeList.length - 1; i >= 0; i--) {
        redSmokeList[i].stateTime += delta;
        smokeAnimation.animation
          .getKeyFrame(redSmokeList[i].stateTime, 1)
          .draw(
            batch,
            redSmokeList[i].x - SMOKE_WIDTH / 2.5,
            redSmokeList[i].y - SMOKE_HEIGHT / 3,
            SMOKE_WIDTH,
            SMOKE_HEIGHT,
            SMOKE_WIDTH / 2,
            SMOKE_HEIGHT / 2,
            0,
            redSmokeList[i].scale,
            redSmokeList[i].scale
          );
        if (
          redSmokeList[i].stateTime >
          smokeAnimation.duration * smokeAnimation.frameLength
        ) {
          if (emptyLife && i === redSmokeList.length - 1) {
            worldState.gameOver = true;
          }
          redSmokeList.splice(i, 1);
        }
      }
      batch.end();
    },
  };
};

export default createRedSmokeRenderSystem;
