const shuffle = (array) => {
  array.sort(() => Math.random() - 0.5);
};

let randomArray, tmp, tmp2, otherFoodArray;
const getRandomFoodList = (foodTex, recipes, comboCounter, difficulty) => {
  randomArray = [];
  tmp = [];
  tmp2 = [];
  otherFoodArray = [];

  for (let rep of recipes) {
    randomArray.push(rep.type);
    tmp.push(rep.type);
  }

  for (let i = 0; i < foodTex.length; i++) {
    tmp2.push(foodTex[i].index);
  }

  for (let value of tmp2) {
    if (!tmp.includes(value)) {
      otherFoodArray.push(value);
    }
  }
  if (comboCounter >= 5 && comboCounter < 10) {
    randomArray.push(otherFoodArray[0]);
  }

  if (comboCounter >= 10 && comboCounter < 20) {
    for (let i = 0; i < otherFoodArray.length - 1; i++) {
      randomArray.push(otherFoodArray[i]);
    }
  }

  if (comboCounter >= 20) {
    for (let i = 0; i < otherFoodArray.length; i++) {
      randomArray.push(otherFoodArray[i]);
    }
  }

  return randomArray;
};

const getDelayBaseOnDifficulty = (difficulty) => {
  if (difficulty >= 1 && difficulty <= 3) {
    return 2.5;
  } else if (difficulty >= 4 && difficulty <= 6) {
    return 2;
  } else if (difficulty >= 7 && difficulty <= 9) {
    return 1.5;
  } else {
    return 1;
  }
};

const getPattern = (number, width, height) => {
  let timer;
  let upSpeedX = [];
  let upSpeedY;
  let upAccelerationY;
  let time;
  let tmpSpeedX = [];
  let tmpAccX = [];

  if (number === 0) {
    // 3 on 1 line
    timer = [0, 0.15, 0.2];
    upSpeedY = [height / 2];
    upAccelerationY = -height / 4;
    time = -upSpeedY / upAccelerationY;
    if (Math.random() > 0.5) {
      tmpSpeedX = [width / 3];
    } else {
      tmpSpeedX = [-width / 3];
    }
  } else if (number === 3) {
    // 4 zig zag
    timer = [0, 0.15, 0.17, 0.25];
    upSpeedY = [height / 2];
    upAccelerationY = -height / 4;
    time = -upSpeedY / upAccelerationY;
    if (Math.random() > 0.5) {
      tmpSpeedX = [width / 3, width / 10, -width / 10, -width / 3];
    } else {
      tmpSpeedX = [-width / 3, -width / 10, width / 10, width / 3];
    }
  } else if (number === 4) {
    // 5 V
    timer = [0, 0.16, 0.16, 0.17, 0.17];
    upSpeedY = [height / 2.1];
    upAccelerationY = -height / 4;
    time = -upSpeedY / upAccelerationY;
    tmpSpeedX = [width / 100, width / 6, -width / 6, width / 2.5, -width / 2.5];
  } else if (number === 5) {
    // 5 V reverse
    timer = [0, 0, 0.12, 0.12, 0.2];
    upSpeedY = [height / 2.1];
    upAccelerationY = -height / 4;
    time = -upSpeedY / upAccelerationY;
    tmpSpeedX = [width / 2.5, -width / 2.5, width / 6, -width / 6, width / 100];
  } else if (number === 1) {
    // 2 random index same time
    timer = [0, 0.15];
    upSpeedY = [height / 2];
    upAccelerationY = -height / 4;
    time = -upSpeedY / upAccelerationY;
    if (Math.random() > 0.5) {
      tmpSpeedX = [
        Math.random() * (width / 4 - width / 8) + width / 8,
        -(Math.random() * (width / 4 - width / 8) + width / 8),
      ];
    } else {
      tmpSpeedX = [
        -(Math.random() * (width / 4 - width / 8) + width / 8),
        Math.random() * (width / 4 - width / 8) + width / 8,
      ];
    }
  } else if (number === 2) {
    // 3 random index same time
    timer = [0, 0.2, 0.1];
    upSpeedY = [height / 2];
    upAccelerationY = -height / 4;
    time = -upSpeedY / upAccelerationY;
    tmpSpeedX = [
      Math.random() * (width / 4 - width / 6) + width / 6,
      -(Math.random() * (width / 2.8 - width / 5.5) + width / 5.5),
      Math.random() * (width / 20 - width / 40) + width / 40,
    ];
  }

  for (let i = 0; i < tmpSpeedX.length; i++) {
    tmpAccX.push(-tmpSpeedX[i] / time);
  }

  for (let i = 0; i < tmpSpeedX.length; i++) {
    upSpeedX.push({ speedX: tmpSpeedX[i], accerX: tmpAccX[i] });
  }
  return {
    timer,
    upSpeedX,
    upSpeedY,
    upAccelerationY,
    time,
  };
};

const getShootTypeBaseOnDifficulty = (difficulty) => {
  if (difficulty >= 1 && difficulty <= 2) {
    return { minNumber: 0, maxNumber: 2 };
  } else if (difficulty >= 3 && difficulty <= 7) {
    return { minNumber: 0, maxNumber: 3 };
  } else {
    return { minNumber: 0, maxNumber: 5 };
  }
};

const createFoodSpawnSystem = () => {
  let index = 0;
  let delayAfterCombo = 2;
  let pattern;
  let timer;
  let speedXIndex = 0;
  let speedYIndex = 0;
  return {
    init(worldState, extra) {},

    update(delta, worldState, { width, height }) {
      // console.log(worldState.foods);
      // console.log(worldState.stateTime, delayAfterCombo, timer);
      const { foodTex, difficulty, recipes, comboCounter } = worldState;
      if (pattern === undefined && worldState.stateTime > delayAfterCombo) {
        const { minNumber, maxNumber } = getShootTypeBaseOnDifficulty(
          difficulty
        );
        pattern = getPattern(
          Math.round(Math.random() * (maxNumber - minNumber) + minNumber),
          width,
          height
        );
        // pattern = getPattern(5, width, height);

        timer = worldState.stateTime + pattern.timer[index];
      } else if (
        worldState.stateTime > delayAfterCombo &&
        worldState.stateTime > timer
      ) {
        index = Math.min((index += 1), pattern.timer.length);
        timer = worldState.stateTime + pattern.timer[index];

        let loop = 1;
        let tmpIndex = index - 1;
        while (true) {
          if (
            pattern.timer[tmpIndex] === pattern.timer[tmpIndex + 1] &&
            tmpIndex < pattern.timer.length
          ) {
            tmpIndex = Math.min((tmpIndex += 1), pattern.timer.length);
            loop += 1;
          } else {
            break;
          }
        }
        // console.log(loop, index);
        for (let i = 0; i < loop; i++) {
          if (i > 0) {
            index = Math.min((index += 1), pattern.timer.length);
          }
          timer = worldState.stateTime + pattern.timer[index];
          const randomFoodArray = getRandomFoodList(
            foodTex,
            recipes,
            comboCounter,
            difficulty
          );
          shuffle(randomFoodArray);

          worldState.foods.push({
            stateTime: 0,
            type: randomFoodArray[0],
            x: width / 2,
            y: (height * 2) / 3,
            upTime: worldState.stateTime + pattern.time, // check with game stateTime
            upRealTime: pattern.time,
            oriSpeedX: pattern.upSpeedX[speedXIndex].speedX,
            oriSpeedY: pattern.upSpeedY[speedYIndex],
            upSpeedX: pattern.upSpeedX[speedXIndex].speedX,
            upSpeedY: pattern.upSpeedY[speedYIndex],
            upAccelerationX: pattern.upSpeedX[speedXIndex].accerX,
            upAccelerationY: pattern.upAccelerationY,
            downSpeedY: 0,
            downAcceleration: height / 100,
            scaleSpeed: (1 - 0) / pattern.time,
            opacitySpeed: (1 - 0) / pattern.time,
            oriScale: 0,
            oriOpacity: 0,
          });

          speedXIndex = Math.min(
            (speedXIndex += 1),
            pattern.upSpeedX.length - 1
          );
          speedYIndex = Math.min(
            (speedYIndex += 1),
            pattern.upSpeedY.length - 1
          );
        }

        // last
        if (index === pattern.timer.length) {
          delayAfterCombo =
            worldState.stateTime + getDelayBaseOnDifficulty(difficulty);
          index = 0;
          speedXIndex = 0;
          speedYIndex = 0;
          pattern = undefined;
        }
      }
    },
  };
};

export default createFoodSpawnSystem;
