const createWhiteSmokeRenderSystem = () => {
  let SMOKE_WIDTH, SMOKE_HEIGHT;
  return {
    init({ foodTex }, { width, height }) {
      SMOKE_WIDTH = width / 2;
      SMOKE_HEIGHT = (SMOKE_WIDTH * 3) / 5;
    },

    update(delta, worldState, { batch, camera, width, height }) {
      const { whiteSmokeList, smokeAnimation, trayState } = worldState;
      batch.setProjection(camera.combined);
      batch.begin();
      batch.setColor(1, 1, 1, 1);
      for (let i = whiteSmokeList.length - 1; i >= 0; i--) {
        if (whiteSmokeList[i].inPot) {
          whiteSmokeList[i].stateTime += delta;
          smokeAnimation.animation
            .getKeyFrame(whiteSmokeList[i].stateTime, 1)
            .draw(
              batch,
              trayState.x - trayState.width * 0.8,
              trayState.y - trayState.height * 1.6,
              SMOKE_WIDTH,
              SMOKE_HEIGHT,
              SMOKE_WIDTH / 2,
              SMOKE_HEIGHT / 2,
              0,
              1.3,
              1.3
            );
        } else {
          whiteSmokeList[i].stateTime += delta;
          smokeAnimation.animation
            .getKeyFrame(whiteSmokeList[i].stateTime, 1)
            .draw(
              batch,
              whiteSmokeList[i].x - SMOKE_WIDTH / 2.5,
              whiteSmokeList[i].y - SMOKE_HEIGHT / 3,
              SMOKE_WIDTH,
              SMOKE_HEIGHT,
              SMOKE_WIDTH / 2,
              SMOKE_HEIGHT / 2,
              0,
              whiteSmokeList[i].scale,
              whiteSmokeList[i].scale
            );
        }
        if (
          whiteSmokeList[i].stateTime >
          smokeAnimation.duration * smokeAnimation.frameLength
        ) {
          whiteSmokeList.splice(i, 1);
        }
      }
      batch.end();
    },
  };
};

export default createWhiteSmokeRenderSystem;
